@media (min-width: 800px){
  .grid-container {
    display: grid;
    grid-template-columns: 1fr 800px 50px 1fr ;
    grid-template-rows: 50px 380px 60px 3fr 3fr;
    gap: 1px 1px;
    grid-template-areas:
      ". . . ."
      ". uploadArea . ."
      ". widthInfoArea . ."
      ". imgParent lengthInfo ."
      ". . . .";
    

      background: -webkit-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
      background: -moz-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
      background: -o-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
      background: linear-gradient(to bottom right, #F3F6F1 0%, #D9DED6 100%);
      font-family: 'Roboto', sans-serif;
      color: #31345A;
  }
  .imgParent {
    grid-area: imgParent;
    width: 800px;
  }
  .uploadArea {
    display: grid;
    grid-template-columns: 4fr 3fr 2fr 2fr 3fr;
    grid-template-rows: 80px 80px 200px;
    gap: 5px 20px;
    grid-template-areas:
      "logo logo . . ."
      "upload position1 position2 inputTile toggle "
      "hex hex hex hex hex";
    grid-area: uploadArea;
  }
  .tileCount{
    display: grid;
    grid-template-columns: 23px 1fr;
    justify-content: left;
    align-items: center;
  }

  input[type="color"] {
    -webkit-appearance: none;
    border: none;
    width: 15px;
    height: 15px;
    
  }
  input[type="color"]::-webkit-color-swatch-wrapper {
    padding: 0;
  }
  input[type="color"]::-webkit-color-swatch {
    border: none;
    border-radius: 2px;
  }

  .logo { 
    display: grid; 
    grid-template-columns: 300px 400px;
    grid-template-areas:
      "logoTitle logoDescription";
    grid-area: logo;}
  
  .logoTitle{
    display: grid;
    justify-content: left;
    grid-area: logoTitle;
    align-items: center;
  }

  .logoDescription{
      display: grid;
      justify-content: left;
      align-items: center;
      grid-area: logoDescription;
  }
  

  .upload { 
    grid-area: upload;
    display: grid; 
    justify-content: left; 
    align-items: center;
  }

  .hex { 
    display: grid;
    grid-area: hex; 
    justify-content: right; 
  }

  #hexChild {
    min-width: 800px;
    justify-content: left; 
  }


  .toggle { 
    grid-area: toggle;
    display: grid; 
    justify-content: right;
  }
  .inputTile {
    grid-area: inputTile;
    display: grid; 
    justify-content: right;
  }
  .position1 { 
    grid-area: position1;
    display: grid; 
    justify-content: right;
  }
  .position2 { 
    grid-area: position2;
    justify-content: right; 
    align-items: center;
  }

  .total { 
    display: grid;
    grid-area: total;
    justify-content: right; 
  }

  .widthInfoArea{ 
    display: grid; 
    grid-template-columns: 1fr 1fr 1fr;
    grid-area: widthInfoArea;
    grid-template-areas:
      "gridToggle loadingInfo widthInfo";  
  }
  .widthInfo{
    display: grid; 
    grid-area: widthInfo;
    justify-content: right;
    align-items: center;
  }

  .gridToggle{
    display: grid; 
    grid-area: gridToggle;
    justify-content: left;
    
  }


  .loadingInfo{
    display: grid; 
    grid-area: loadingInfo;
    justify-content: center;
  }

  .lengthInfo { 
    display: grid;
    grid-template-columns: 1fr;
    /* grid-template-rows: 1fr; */
    gap: 1px 1px;
    grid-template-areas:
      "length";
    grid-area: lengthInfo;
    justify-content: center;
  }

  .affiliate { display: grid; 
    grid-area: affiliate; 
    align-content: center; 
    justify-content: right; }

  .link{
    color:#EB2F7D;
    text-decoration: none;
  }


  .length {
    display: grid;
    grid-area: length;
    justify-content: center;
    }
    #rotate-text{
      writing-mode: vertical-rl;
      text-orientation: mixed;
      justify-content: center;
    }


  canvas,img {
    display: block;
    width: 100%;
    height: auto;
  }
}








