@media (max-width: 799px) {
    .grid-container {
      display: grid;
      grid-template-columns: 10px 200px 1fr ;
      grid-template-rows: 30px 900px 30px 30px 3fr 3fr;
      gap: 20px 20px;
      grid-template-areas:
        ". .  ."
        ". uploadArea  ."
        ". widthInfoArea ."
        ". lengthInfo ."
        ". imgParent ."
        ". . .";
      
    
        background: -webkit-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
        background: -moz-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
        background: -o-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
        background: linear-gradient(to bottom right, #F3F6F1 0%, #D9DED6 100%);
        font-family: 'Roboto', sans-serif;
        color: #31345A;
    }
  
    .imgParent {
      grid-area: imgParent;
      width: 275px;
    }
  
    .uploadArea {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr 1fr 1fr 1fr 400px;
      gap: 20px 1px;
      grid-template-areas:
        "logo "
        "upload" 
        "inputTile" 
        "inputColor" 
        "hexButton"
        "hex";
      grid-area: uploadArea;
    }
    .tileCount{
        display: grid;
        grid-template-columns: 23px 1fr;
        justify-content: left;
        align-items: center;
      }
    
      input[type="color"] {
        -webkit-appearance: none;
        border: none;
        width: 15px;
        height: 15px;
        
      }
      input[type="color"]::-webkit-color-swatch-wrapper {
        padding: 0;
      }
      input[type="color"]::-webkit-color-swatch {
        border: none;
        border-radius: 2px;
      }
    
      .logo { 
        display: grid; 
        grid-template-rows: 1fr 1fr;
        grid-template-areas:
          "logoTitle" 
          "logoDescription";
        grid-area: logo;}
      
      .logoTitle{
        display: grid;
        justify-content: left;
        grid-area: logoTitle;
        align-items: center;
      }
    
      .logoDescription{
          display: grid;
          justify-content: left;
          align-items: center;
          grid-area: logoDescription;
      }
      .hexButton { 
        grid-area: hexButton;
        display: grid; 
        justify-content: left; 
        align-items: center;
      }
    
      .upload { 
        grid-area: upload;
        display: grid; 
        justify-content: left; 
        align-items: left;
      }
    
      .hex { 
        display: grid;
        grid-area: hex; 
        justify-content: left; 
      }
    
      #hexChild {
        max-width: 200px;
        justify-content: left; 
      }
    
      .inputColor { 
        grid-area: inputColor;
        display: grid; 
        justify-content: left;
      }
    
      .inputTile { 
        grid-area: inputTile;
        display: grid; 
        justify-content: left; 
      }
    
      .total { 
        display: grid;
        grid-area: total;
        justify-content: left; 
      }
    
      .widthInfoArea{ 
        display: grid; 
        gap: 1px 1px;
        grid-template-rows: 1fr 1fr;
        grid-area: widthInfoArea;
        grid-template-areas:
          "widthInfo"
          "loadingInfo"; 
      }

      .widthInfo{
        display: grid; 
        grid-area: widthInfo;
        justify-content: left;
      }
    
      .loadingInfo{
        display: grid; 
        grid-area: loadingInfo;
        justify-content: left;
      }
    
      .lengthInfo { 
        display: grid;
        grid-template-columns: 1fr;
        gap: 1px 1px;
        grid-template-areas:
          "length";
        grid-area: lengthInfo;
        justify-content: left;
      }

      .length {
        display: grid;
        grid-area: length;
        justify-content: left;
        }
    
      .affiliate { display: grid; 
        grid-area: affiliate; 
        align-content: center; 
        justify-content: right; }
    
      .link{
        color:#EB2F7D;
        text-decoration: none;
      }

      canvas,img {
        display: block;
        width: 100%;
        height: auto;
      }

      body{
          color: #E6EAE4;
      }
  }